/* ChatStandalone full-screen dark theme */
body {
  margin: 0;
  padding: 0;
  background-color: #343541;
  color: white;
  font-family: 'Inter', sans-serif;
  height: 100vh;
  overflow: hidden;
}

  /* Chat Container */
  .chat-container {
    display: flex;
    width: 100vw;
    height: 100vh;
  }

.chatbox {
  height: 100vh;
  width: 100vw;
  background: #1B1C1D;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
  position: relative;
}

.messages {
  max-height: calc(100vh - 150px); /* Adjust based on layout */
  flex-grow: 1;
  overflow-y: auto;
  padding: 30px 8% 40px 8%;
  display: flex;
  flex-direction: column;
  position: relative;
  scroll-behavior: smooth;
}

/* Scrollbar styling */
.messages::-webkit-scrollbar {
  width: 8px;
}
.messages::-webkit-scrollbar-thumb {
  background: #555;
  border-radius: 10px;
}
.messages::-webkit-scrollbar-track {
  background: #2c2c2e;
}

/* Message Bubbles */
.message.user {
  align-self: flex-end;
  background: #333537;
  color: #dcd7d7;
  text-align: right;
  max-width: 75%;
  padding: 12px 16px;
  border-radius: 30px 0 30px 30px;
  margin: 5px 0;
  font-size: 18px;
  line-height: 1.4;
}

.message.bot {
  align-self: flex-start;
  background: #1B1C1D;
  color: #dcd7d7;
  text-align: left;
  max-width: 75%;
  padding: 12px 16px;
  border-radius: 0 30px 30px 30px;
  margin: 5px 0;
  font-size: 18px;
  line-height: 1.4;
}




/* Responsive Sidebar Transition */
.sidebar {
  width: 300px;
  background: #202123;
  padding: 20px;
  transition: width 0.3s ease-in-out;
  overflow: hidden;
  height: 100vh;
}

.sidebar.collapsed {
  width: 50px;
  padding: 10px;
}

.sidebar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.toggle-btn {
  background: none;
  border: none;
  font-size: 24px;
  color: white;
  cursor: pointer;
}

.sidebar.collapsed .new-chat-btn,
.sidebar.collapsed .search-btn,
.sidebar.collapsed .chat-history {
  display: none;
}



.chat-standalone-loading,
.chat-standalone-error {
  color: #ccc;
  text-align: center;
  padding: 20px;
}



.input-area {
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 20px;
  background: #303030;
  border: 1px solid #ffffff;
  width: 80%;
  align-self: center;
  margin-top: 10px;
}

.input-area textarea {
  flex: 1;
  padding: 10px;
  border-radius: 5px;
  font-size: 20px;
  border: none;
  background: #303030;
  color: white;
  resize: none;
  outline: none;
  height: auto;
  min-height: 30px;
  max-height: 150px;
  overflow-y: auto;
}

.input-area button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 22px;
  color: white;
  margin-right: 10px;
  padding: 5px;
}

.input-area button:hover {
  color: #1db954; /* or any nice highlight color */
}

.input-area button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.mic-btn:hover,
.send-btn:hover {
  transform: scale(1.3);
}


/* Scroll to Bottom Button (Centered in Chat Area) */
.scroll-bottom-btn {
  position: absolute;
  bottom: 140px; /* Adjust as needed */
  left: 50%;
  transform: translateX(-50%);
  background: #212121;
  color: rgb(216, 212, 212);
  border-color: white;
  border-width: 2px;
  padding: 10px 15px;
  border-radius: 50%; /* Ensures circular shape */
  cursor: pointer;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

  
.scroll-bottom-btn:hover {
  background: #4d4c4c;
}
