/* 🌟 Forgot Password Page - Full Screen 🌟 */

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

body, html {
  width: 100%;
  height: 100%;
  overflow: hidden; /* Prevent scrolling */
  overscroll-behavior: none; /* prevents scroll bounce on mobile */
}

.forgot-password-container {
  overflow: hidden; /* key to stopping inner scroll */
  display: flex;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(to right, #101418, #181c20);
}

.forgot-password-box {
  flex: 1;
  background: rgba(30, 31, 34, 0.98);
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.6);
  max-width: 450px;
  margin: auto;
  transition: transform 0.3s;
}

.forgot-password-box:hover {
  transform: scale(1.02);
}

.forgot-password-box h2 {
  color: #ffffff;
  font-size: 26px;
  margin-bottom: 20px;
  font-weight: 600;
}

.input-group {
  margin-bottom: 18px;
  text-align: left;
}

.input-group label {
  display: block;
  color: #bbbbbb;
  font-size: 14px;
  margin-bottom: 6px;
}

.input-group input {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 6px;
  background: #2b2d31;
  color: #ffffff;
  font-size: 15px;
  outline: none;
  transition: 0.3s;
}

.input-group input:focus {
  background: #33353a;
  border: 1px solid #3c76ff;
}

.forgot-password-btn {
  width: 100%;
  padding: 12px;
  background: #3c76ff;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 17px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.forgot-password-btn:hover {
  background: #2c5cd8;
}

.success-message {
  color: green;
  font-size: 14px;
  margin-top: 12px;
}

.error-message {
  color: red;
  font-size: 14px;
  margin-top: 12px;
}

.forgot-password-link {
  text-align: right;
  margin-top: 8px;
  font-size: 0.9rem;
}

.forgot-password-link a {
  color: #007bff; /* Bootstrap blue or your primary color */
  text-decoration: none;
  transition: color 0.2s ease-in-out;
}

.forgot-password-link a:hover {
  color: #0056b3; /* Darker on hover */
  text-decoration: underline;
}

/* 🌟 Right Side - Image & Text 🌟 */
.forgot-password-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  padding: 40px;
  max-width: 50%;
}

.forgot-password-info h1 {
  font-size: 34px;
  margin-bottom: 10px;
  font-weight: 700;
}

.forgot-password-info p {
  font-size: 18px;
  margin-bottom: 20px;
  color: #bbb;
}

.forgot-password-info img {
  max-width: 60%;
  height: auto;
  animation: float 3s ease-in-out infinite;
}

/* Floating Animation */
@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

/* 📱 Responsive Design */
@media (max-width: 900px) {
  .forgot-password-container {
    flex-direction: column;
  }

  .forgot-password-box {
    width: 90%;
    margin-bottom: 20px;
  }

  .forgot-password-info {
    max-width: 90%;
    padding: 20px;
  }

  .forgot-password-info img {
    max-width: 60%;
  }
}

@media (max-width: 768px) {
  .forgot-password-container {
    flex-direction: column;
    overflow-y: auto;
  }

  .forgot-password-box,
  .forgot-password-info {
    max-width: 100%;
    width: 100%;
    padding: 40px;
    border-radius: 0;
  }

  .forgot-password-box {
    box-shadow: none;
  }

  .forgot-password-info h1 {
    font-size: 26px;
  }

  .forgot-password-info p {
    font-size: 16px;
  }

  .forgot-password-info img {
    max-width: 80%;
  }
}

@media (max-width: 480px) {
  .forgot-password-info h1 {
    font-size: 22px;
  }

  .forgot-password-info p {
    font-size: 14px;
  }

  .input-group input {
    font-size: 14px;
  }

  .forgot-password-btn {
    font-size: 15px;
  }
}
