.success-container {
    text-align: center;
    padding: 60px 20px;
    background-color: #f4f9ff;
    border-radius: 10px;
    max-width: 600px;
    margin: 80px auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  }
  
  .success-container h1 {
    color: #2e7d32;
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  .success-container p {
    font-size: 1.2rem;
    color: #555;
  }
  