/* Root styles */
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #212121;
  font-family: 'Segoe UI', sans-serif;
  display: flex;
  justify-content: center;
  align-items: flex-start; /* Align to the top */
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

/* Allow body to scroll if content overflows vertically */
body {
  overflow-y: auto; /* Allow vertical scrolling */
}

/* Container */
.pricing-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; /* Align content to the top */
  align-items: center;
  padding: 40px 20px;
  box-sizing: border-box;
  min-height: 100vh; /* Allow the container to take full height */
  overflow-y: auto; /* Enable vertical scrolling */
  width: 100%; /* Ensure it takes full width */
}

/* Page title */
.pricing-title {
  font-size: 2.5rem;
  color: #dbdbdc;
  margin-bottom: 40px;
  text-align: center;
}

/* Card layout */
.pricing-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  gap: 30px;
  max-width: auto;
  margin: 0 auto;
  width: 100%;
  padding-bottom: 40px; /* To give space for scrolling */
}

/* Each card */
.pricing-card {
  background-color: #212121;
  border: solid 1px white !important;
  border-radius: 12px;
  box-shadow: 0 4px 12px rgba(255, 255, 255, 0.1);
  width: 400px;
  padding: 30px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box; /* Ensures proper box model */
}

/* Card title */
.plan-name {
  font-size: 1.4rem;
  font-weight: bold;
  color: #c4c6cb;
  margin-bottom: 10px;
}

/* Price text */
.plan-price {
  font-size: 2rem;
  font-weight: bold;
  margin: 0;
  color: #c4c6cb;
}

/* Subtext below price */
.plan-billing {
  color: #6b6b6b;
  margin-bottom: 20px;
}

/* Features list */
.plan-features {
  list-style: none;
  padding-left: 0;
  margin-bottom: 30px;
  color: #939496;
}

.plan-features li {
  margin-bottom: 8px;
}

/* Upgrade button */
.plan-button {
  padding: 12px;
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  font-size: 1rem;
  transition: 0.3s ease;
}

.plan-button:hover {
  opacity: 0.85;
}


/* Badge for current plan */
/* Badge style */
.current-plan-badge {
  position: absolute;
  top: 15px;
  left: -15px;
  background-color: #ff5722;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 0.8rem;
  font-weight: bold;
  transform: rotate(-15deg); /* Slight tilt to the left */
  transform-origin: top left; /* Set the point of rotation to top left */
  z-index: 10; /* Ensure the badge is on top */
}



/* 📱 Mobile - Small devices */
@media (max-width: 600px) {
  html, body {
    height: auto;
  }

  .pricing-wrapper {
    padding: 20px;
    justify-content: flex-start;
    min-height: auto;
  }

  .pricing-title {
    font-size: 2rem;
  }

  .pricing-cards {
    gap: 15px;
    padding: 0 10px;
    width: 100%;
    margin-bottom: 20px;
  }

  .pricing-card {
    width: 100%;
    max-width: 350px; /* Ensures that the card is properly responsive */
    padding: 20px;
    margin-bottom: 20px;
  }

  .plan-name {
    font-size: 1.2rem;
  }

  .plan-price {
    font-size: 1.6rem;
  }

  .plan-button {
    font-size: 0.95rem;
    padding: 10px;
  }
}

/* 📱 Tablets */
@media (min-width: 601px) and (max-width: 992px) {
  .pricing-wrapper {
    padding: 30px;
  }

  .pricing-cards {
    gap: 20px;
  }

  .pricing-card {
    max-width: 320px;
  }

  .plan-name {
    font-size: 1.3rem;
  }

  .plan-price {
    font-size: 1.8rem;
  }

  .plan-button {
    font-size: 1rem;
    padding: 12px;
  }
}

/* 🖥 Desktop */
@media (min-width: 993px) {
  .pricing-wrapper {
    padding: 40px;
  }

  .pricing-cards {
    gap: 30px;
  }

  .pricing-card {
    width: 400px;
  }
}
